export const getFormattedDays = (days: number) => {
    const formatter = new Intl.PluralRules("ru-RU");
    const suffixes = {
        zero: "",
        one: "",
        two: "",
        few: "",
        many: "",
        other: "",
    };

    return `${days} ${suffixes[formatter.select(days)]}`;
};
